import React from "react";
import ReactDOM from "react-dom";

var canvasWidth = 101
var canvasHeight = 101
var totalPoints = 4*canvasWidth*canvasHeight

function renderSquare(x,y) {
  y = canvasHeight - y - 1;
  return 4*(parseInt(x, 10)+parseInt(y, 10)*canvasWidth)

}


class Canvas extends React.Component {
  constructor(props) {
    super(props);
    this.state = {x: "",
                  y: ""};

    this.handleXChange = this.handleXChange.bind(this);
    this.handleYChange = this.handleYChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(event) {
    if (this.state.x !== "" && this.state.y !== "") {
      const canvas = this.refs.canvas
      const ctx = canvas.getContext("2d")
      var imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
      var data = imageData.data;
      var index = renderSquare(this.state.x,this.state.y);
      data[index+3]=0;
      ctx.putImageData(imageData, 0, 0);
    }
    else {
      this.componentDidMount()
    }
  }
  // handleXChange(event) {
  //   const canvas = this.refs.canvas
  //   const ctx = canvas.getContext("2d")
  //   console.log(event.target.value)
  //   if (event.target.value === "" && this.state.y === "") {
  //     console.log('in here')
  //     ctx.fillStyle = 'black';
  //     ctx.fillRect(0, 0, canvas.width, canvas.height);
  //     this.setState({x: ''});
  //   }
  //   else {
  //     this.setState({x: event.target.value});
  //     var imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
  //     var data = imageData.data;
  //     var index = renderSquare(event.target.value,this.state.y);
  //     data[index+3]=0;
  //     ctx.putImageData(imageData, 0, 0);
  //   }
  // }
  //
  handleYChange(event) {
      this.setState({y: event.target.value});
  }

  handleXChange(event) {
      this.setState({x: event.target.value});
  }

  componentDidMount() {
    const canvas = this.refs.canvas;
    const ctx = canvas.getContext("2d");
    ctx.fillStyle = 'black';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    // var imageData = ctx.getImageData(0, 0, canvas.width, canvas.height);
    // var data = imageData.data;
    // ctx.putImageData(imageData, 0, 0);
    }
  render() {
    return(
      <div>
        <canvas ref="canvas" width={canvasWidth} height={canvasHeight} />
        <br/>
        x:
        <input type="text" value={this.state.x} onChange={this.handleXChange}/>
        <br/>
        y:
        <input type="text" value={this.state.y} onChange={this.handleYChange}/>
        <br/>
        <button onClick={this.handleSubmit}>GO</button>
        <br/>
        Bottom Left is (0,0). Max is 100.
      </div>
    )
  }
}
ReactDOM.render(<Canvas />, document.getElementById("root"));



